body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  min-height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.react-ex__header {
  background: #33313b;
  width: 100%;
  color: #fdc57b;
  height: 50px;
  line-height: 50px;
  padding: 0 20px;
  text-transform: uppercase;
  text-align: right;
  border-bottom: 1px solid #fff;
}

.react-ex__sidebar {
  background: #fdc57b;
  padding: 0;
}

.react-ex__sidebar ul {
  list-style: none;
  padding-left: 0;
}

.react-ex__sidebar ul li {
  padding: 10px;
  border-bottom: 1px solid #fff;
}

.react-ex__sidebar ul li a {
  color: #33313b;
  text-decoration: none;
}

.react-ex__content {
  padding-top: 30px;
}

.react-ex__footer {
  width: 100%;
  padding: 0 10px;
  height: 50px;
  line-height: 50px;
  background: #eee;
  border-top: 1px solid #fff;
}

.datetime {
  width: 60%;
  margin: 0 auto;
}

.countdown-content {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.countdown-box {
  text-align: center;
  margin: 0 20px;
}

.countdown-box__number {
  font-size: 50px;
  font-weight: bold;
  width: 150px;
  height: 150px;
  border: 1px solid;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.datetime__input {
  margin-bottom: 30px;
}

.datetime__input::-webkit-clear-button {
  -webkit-appearance: none;
  display: none;
}

